import Vue from "vue"
import verify from "../views/web.vue"
import VueRouter from "vue-router"
import page404 from "../views/404.vue"

Vue.use(VueRouter)
const router = new VueRouter({
	mode: 'history',
	routes: [{
		path: "/verification",
		name: 'geetest',
		component: verify
	}, {
		path: "/",
		name: 'home',
		redirect: "/verification"
	}, {
		path: "*",
		name: '404',
		component: page404
	}]
})

export default router