<template>
	<div>
		<div>
			<h1>请点击进行验证</h1>
		</div>
		<div id="captcha">
			<p id="wait" class="show">正在加载验证码......</p>
		</div>
	</div>
</template>
<script>
	import "../lib/gt.js"
	import "../lib/jquery.js"
	import {
		getHeaders
	} from "../tool/ds.js"
	import md5 from "md5"

	export default {
		name: "web",
		mounted() {
			let param = this.$route.query
			if (!param.gt || !param.challenge || !param.success) {
				$("#wait")[0].innerText = "参数不全，请重新获取"
				return
			}
			let p = {
				gt: param.gt,
				challenge: param.challenge,
				offline: !param.success
			}
			if (Object.keys(param).includes("new_captcha")) p.new_captcha = param.new_captcha
			initGeetest(p, function(captchaObj) {
				captchaObj.appendTo("#captcha")
				captchaObj.onReady(function() {
					$("#wait").hide();
				});
				captchaObj.onSuccess(function() {
					let result = captchaObj.getValidate()
					let h = md5(`challenge=${param.challenge}&gt=${param.gt}`)
					$.ajax({
						url: "/result/success",
						type: "post",
						dataType: "json",
						headers: {
							"Content-Type": "application/json;charset=utf-8;"
						},
						data: JSON.stringify({
							key: h,
							data: result
						}),
						success: function(data) {
							if (data.code == 0) alert("验证成功")
							else alert("数据发送失败")
						},
						error() {
							alert("数据发送失败")
						}
					})
				})
			})
		}
	}
</script>

<style>
	#captcha {
		display: flex;
		justify-content: center;
	}

	div {
		text-align: center;
	}
</style>